<template>
  <div id="app" v-on:click="next" v-touch:swipe.Up="swipeUpHandler">
    <Header></Header>
    <!--{{currentStory}}-->
    <News :titular="titular" :hashes="hashes" :persistTags="persistTags" :displayTags="displayTags" :link="link" :fuente="fuente" :time="time" :current="currentContent"></News>
    <Skip></Skip>
  </div>
</template>

<script>
import Header from './components/Header.vue'
import News from './components/News.vue'
import Skip from './components/Skip.vue'
import axios from 'axios'
import '@fortawesome/fontawesome-free/css/all.css'
import '@fortawesome/fontawesome-free/js/all.js'


export default {
  name: 'App',
  components: {
    Header,
    News,
    Skip
  },
  data(){
    return{
      mainJson: {},
      currentStory:0,
      currentContent:0,
      currentTag:0,
      persistTags:[],
      titular:[],
      hashes:[],
      link:'',
      fuente:'',
      time:'',
      displayTags:false,

    }
  },
  mounted:function(){
    this.fetchData();
  },
  methods: {
    fetchData(){
      axios.get('/static/latest.json').then(response => {

        this.mainJson = response.data;
        //this.display = '#'+this.mainJson.stories[0].tags[0];
        this.hashes.push(decodeURIComponent(this.mainJson.stories[this.currentStory].tags[0]));
      })
     },
    swipeUpHandler:function()
    {
      //console.log('swipe')
       if(this.mainJson.stories.length > (this.currentStory+1))
      {
        this.displayTags = false; 
        this.currentStory++;
        this.currentContent = 0;
        this.currentTag = 0;
        this.titular = [];
        this.hashes = [decodeURIComponent(this.mainJson.stories[this.currentStory].tags[0])];
        return;
      }
    },
    next: function (/*magia*/) {
      this.titular = [];
      this.displayTags = false; 
      //console.log(this.mainJson.stories[this.currentStory].tags.length)

      if(this.mainJson.stories[this.currentStory].tags.length > (this.currentTag+1))
      {
        this.currentTag++;
        this.titular = [];//' - #'+this.mainJson.stories[this.currentStory].tags[this.currentTag];
        
        this.hashes.push(decodeURIComponent(this.mainJson.stories[this.currentStory].tags[this.currentTag]))
        if(this.currentTag+1 == this.mainJson.stories[this.currentStory].tags.length)
        {
          this.currentContent = -1;
          this.persistTags = this.hashes;
        }
        return;
      }

      if(this.mainJson.stories[this.currentStory].content.length > (this.currentContent+1))
      {
        this.displayTags = true; 
        this.currentContent++;
        this.fuente = this.mainJson.stories[this.currentStory].content[this.currentContent].fuente;
        this.time = this.mainJson.stories[this.currentStory].content[this.currentContent].time;
        this.link = this.mainJson.stories[this.currentStory].content[this.currentContent].link;
        this.titular = [decodeURIComponent(this.mainJson.stories[this.currentStory].content[this.currentContent].title)]; 
        this.hashes = []//this.mainJson.stories[this.currentStory].tags;
        
        return;
      }

      if(this.mainJson.stories.length > (this.currentStory+1))
      {
        this.currentStory++;
        this.currentContent = 0;
        this.currentTag = 0;
        this.titular = [];
        this.hashes = [decodeURIComponent(this.mainJson.stories[this.currentStory].tags[0])];
        return;
      }
      //this.currentStory++;
    }
  }
}

</script>

<style>
body
{
  margin:0;
}

#app {
  margin: 0 auto;
  max-width: 720px;
  box-sizing: border-box;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0;
  height:100vh;
  width: 100vw;
  padding: 2em;
  background-color: white;
  background-image: url(assets/so-white.png);
  padding-top: 65px;
}
</style>



