<template>
    <div>
        <div class="titular">
         <!-- <p v-for="titulo in titular">{{titulo}}</p>-->
         <div v-if="!displayTags" class="fadeInUpBig" >
           <p class="hash fadeInUpBig" v-for="hash in hashes">#{{ hash }}</p>
         </div>
         <div v-if="displayTags" class="fadeInUpBig">
          <p v-html="type" class="tit"></p>
          </div>
          <div class="tags fadeIn" v-if="displayTags">
            
            <div class="fuente">{{time}}</div>
            <Twitter  v-if="current>=3" class="tw fadeInDown" :twhash="persistTags[0]"></Twitter>
            <div class="fuente"><b>Fuente:</b> {{fuente}} > <a v-bind:href="link" target="_blank">Ver noticia completa</a></div>
            <p v-for="persistedTag in persistTags">#{{ persistedTag }}</p>
          </div>
        
          
        </div>
       
    </div>
</template>

<script>
import Twitter from './Twitter.vue'
export default {
  name: 'News',
  components: {
    Twitter
  }, 
  props: {
    titular: Array,
    hashes: Array,
    persistTags:Array,
    displayTags:Boolean,
    link:String,
    fuente:String,
    time:String,
    current:Number
  },
  data(){
    return{
      pulseActive:true,
      type:'',
      typeI:0
    }
  },
  methods:
  {
    typeWrite:function()
    {
      if (this.typeI < this.titular[0].length) {
        this.type += this.titular[0].charAt(this.typeI);
        this.typeI++;
        setTimeout(()=>{this.typeWrite()}, 35);
      }
    }
  },
  watch: {
    titular: function (val) 
    {
      //this.pulseActive = false;
      if(typeof val[0] == 'undefined')
      {
        this.type="";
        this.typeI = 0;
        return;
      }

      this.type="";
      this.typeI = 0;
      this.typeWrite();        
    }
  }
}
//console.log(this._data.mainJson.stories)
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap');


.titular
{ 
  
  color: #111111; 
  height: 50vh;
  display: table-cell;
  vertical-align: middle;
  font-family: 'Playfair Display', serif;
  font-size: 30px;
}

.fuente
{
  font-family: 'Libre Baskerville', serif; 
  font-size: 14px;
  margin: 3px;
  text-align: left;
  margin:5px 0;
}

.tit
{
  height: 40vh;
  vertical-align: middle;
  display: table-cell;
}

.hash
{ 
  color: #333333; 
  border:1px solid #B68C2F;
  font-size: 18px;
  float: left; 
  padding: 10px; 
  margin: .2em; 
  font-family: 'Libre Baskerville', serif; 
  -webkit-box-shadow: 10px 10px 24px -1px rgba(0,0,0,0.1);
-moz-box-shadow: 10px 10px 24px -1px rgba(0,0,0,0.1);
box-shadow: 10px 10px 24px -1px rgba(0,0,0,0.1);
}

.tags
{
  color: #333333; 
  font-family: 'Libre Baskerville', serif; 
  width:100%;
}

.tags p
{
  border:1px solid #B68C2F;
  font-size: 16px;
  padding: 6px; 
  display: inline-block;
  margin: 1px;
  -webkit-box-shadow: 10px 10px 24px -1px rgba(0,0,0,0.1);
-moz-box-shadow: 10px 10px 24px -1px rgba(0,0,0,0.1);
box-shadow: 10px 10px 24px -1px rgba(0,0,0,0.1);
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
  }
  @keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
  } 

.pulse {
  -webkit-animation-name: pulse;
  animation-name: pulse;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes pulse {
  0% {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  }
  50% {
  -webkit-transform: scale3d(1.05, 1.05, 1.05);
  transform: scale3d(1.05, 1.05, 1.05);
  }
  100% {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  }
  }
  @keyframes pulse {
  0% {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  }
  50% {
  -webkit-transform: scale3d(1.05, 1.05, 1.05);
  transform: scale3d(1.05, 1.05, 1.05);
  }
  100% {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  }
  } 

.flipInY {
  -webkit-backface-visibility: visible !important;
  backface-visibility: visible !important;
  -webkit-animation-name: flipInY;
  animation-name: flipInY;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes flipInY {
  0% {
  -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
  transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  opacity: 0;
  }
  40% {
  -webkit-transform: perspective(400px) rotate3d(0, 1, 0, -20deg);
  transform: perspective(400px) rotate3d(0, 1, 0, -20deg);
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  }
  60% {
  -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 10deg);
  transform: perspective(400px) rotate3d(0, 1, 0, 10deg);
  opacity: 1;
  }
  80% {
  -webkit-transform: perspective(400px) rotate3d(0, 1, 0, -5deg);
  transform: perspective(400px) rotate3d(0, 1, 0, -5deg);
  }
  100% {
  -webkit-transform: perspective(400px);
  transform: perspective(400px);
  }
  }
  @keyframes flipInY {
  0% {
  -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
  transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  opacity: 0;
  }
  40% {
  -webkit-transform: perspective(400px) rotate3d(0, 1, 0, -20deg);
  transform: perspective(400px) rotate3d(0, 1, 0, -20deg);
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  }
  60% {
  -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 10deg);
  transform: perspective(400px) rotate3d(0, 1, 0, 10deg);
  opacity: 1;
  }
  80% {
  -webkit-transform: perspective(400px) rotate3d(0, 1, 0, -5deg);
  transform: perspective(400px) rotate3d(0, 1, 0, -5deg);
  }
  100% {
  -webkit-transform: perspective(400px);
  transform: perspective(400px);
  }
  }

  .fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes fadeInDown {
  0% {
  opacity: 0;
  -webkit-transform: translate3d(0, -100%, 0);
  transform: translate3d(0, -100%, 0);
  }
  100% {
  opacity: 1;
  -webkit-transform: none;
  transform: none;
  }
  }
  @keyframes fadeInDown {
  0% {
  opacity: 0;
  -webkit-transform: translate3d(0, -100%, 0);
  transform: translate3d(0, -100%, 0);
  }
  100% {
  opacity: 1;
  -webkit-transform: none;
  transform: none;
  }
  } 

.fadeInUpBig {
  -webkit-animation-name: fadeInUpBig;
  animation-name: fadeInUpBig;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes fadeInUpBig {
  0% {
  opacity: 0;
  -webkit-transform: translate3d(0, 2000px, 0);
  transform: translate3d(0, 2000px, 0);
  }
  100% {
  opacity: 1;
  -webkit-transform: none;
  transform: none;
  }
  }
  @keyframes fadeInUpBig {
  0% {
  opacity: 0;
  -webkit-transform: translate3d(0, 2000px, 0);
  transform: translate3d(0, 2000px, 0);
  }
  100% {
  opacity: 1;
  -webkit-transform: none;
  transform: none;
  }
  } 

</style>


